import React, { useCallback, useState } from "react";
import { Link, useHistory } from "react-router-dom";
import { Api } from "../../utils/Api";
import { setToken } from "../../utils/localstorage";
import "./signIn.css";
function Index() {
  const { replace, push } = useHistory();
  const [email, setEmail] = useState();
  const [password, setPassword] = useState();
  const [loading, setLoading] = useState(false);

  const _handleSubmit = useCallback(async () => {
    // callback
    if (email.length > 2 && password.length > 2) {
      setLoading(true);
      const { statusCode, data } = await Api.postRequest("/api/user/signin", {
        email,

        password,
      });
      setLoading(false);
      if (statusCode === 400 || statusCode === 500 || statusCode === 403) {
        setLoading(false);
        alert(data);
        return;
      }
      const { token } = JSON.parse(data);
      setToken(token);
      replace("/");
    }
  }, [email, password, replace]);

  if (loading) return <h1>Loading.....</h1>;
  return (
    <div className="signinscreen ">
      <div className="container">
        <div className="innerContainer  mx-auto">
          <div
            style={{
              display: "transparent",
              justifyContent: "space-between",
              alignItems: "center",
              marginBottom: "20px",
              // backgroundColor: 'red',
            }}
          >

            <p className="text-success" style={{ marginLeft:"0px"}}>Login</p>
          </div>

          <label for="email">Email</label>
          <input
            type="email"
            id="lname"
            name="email"
            placeholder="Your email.."

            
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />
          <label for="password">Password</label>
          <input
            type="password"
            id="lname"
            name="password"
            placeholder="Your Password.."
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />

          <Link to="/signup" className="link">
            <span>Creat a new account ?</span>
          </Link>
          <br />
          
          <input type="submit" value="Sign in" onClick={_handleSubmit} />
          <span style={{ cursor: "pointer" }} onClick={() => push("/")}>
         
              <button type="button" className="btn btn-outline-danger" style={{ height:"47px" , width:"100px" , marginLeft:"20px"}}>Back</button>
              
            </span>
            
        </div>
        
      </div>
    </div>
  );
}

export default Index;
